



































import { Component, Vue, Prop } from 'vue-property-decorator'

@Component({})
export default class ShopManage extends Vue {
	@Prop({
		type: Boolean,
		default: false
	})
	shoptype: Boolean
	@Prop({
		type: String
	})
	shopTime: string = ''
	@Prop({
		type: Number || String
	})
	shopStatus: Number | String
}
